.navigation-form {
  display: none;
}

.navigation__wrapper {
  @include center;
}

.navigation__list {
  width: 320px;
  height: 198px;
  list-style: none;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  font-family: $main-fonts;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  margin-top: -15px;
  margin-left: -40px;
  outline-offset: -5px;
}

.site-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 66px;
  border-bottom: 1px solid $border-nav;
}

.site-list__item a {
  color: $base-color-text;
  text-decoration: none;
}

@media (min-width: $tablet-width) {
  .navigation__list {
    width: 440px;
    height: 36px;
    display: flex;
    flex-direction: row;
  }
}

@media (min-width: $desktop-width) {
  .navigation__list {
    width: 448px;
    height: 40px;
    display: flex;
    flex-direction: row;
  }
}
